import React, { useState, useEffect } from 'react';
import FooterImg4 from '../../../../assets/imges/body.png'
import Slider from 'react-slick'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { HomeCardImgText2, HomeCardText } from '../../Home/HomeCardImg/style';
import { HomeBox, HomeBox1 } from '../../Home/style';
import { Container } from '../../style';
import axios from 'axios';
import { baseLink } from '../../../../baseLink';



function MostPage() {

    const [state, setState] = useState([])

    const getData = () => {
        axios
            .get(baseLink + "most-popular")
            .then((res) => {
                setState(res.data)
            })
            .catch((err) => {
                console.log(err);
            })
    };

    useEffect(() => {
        getData();
    }, [])

    var settings = {
        // dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 1000,
    };

    return (
        <Container>
            <HomeBox>
                <img src={FooterImg4} alt="#" />
                <HomeBox1>
                    <HomeCardText>Most Popular</HomeCardText>
                    <Slider {...settings}>
                        {state?.map((value) => (
                            <div>
                                <img src={baseLink + "files/" + value.imageId} alt="#" style={{ marginBottom: "20px" }} />
                                <HomeCardImgText2>{value.title} <br /><span>{value.author}| {value.createdAt}</span></HomeCardImgText2>
                            </div>
                        ))}
                    </Slider>
                </HomeBox1>
            </HomeBox>
        </Container>
    )
}
export default MostPage;