import styled from "styled-components";
import HomeCardImg1 from '../../../../assets/imges/trending-3.jpg'

export const HomeCardWrapper=styled.div`
    background-color: white;
    padding: 15px 10px 20px 10px;
    border-radius: 10px;
`;

export const HomeCardText=styled.p`
    font-size: 18px;
    font-weight: bold;
    margin: 15px 0;
    color: #051441;
`;

export const HomeCardText1=styled.p`
    background-image: url(${HomeCardImg1});
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    border-radius: 10px 10px 0 0;
    padding: 150px 25px 25px 25px;
    line-height: 20px;
    font-weight: bold;
    color: white;
    span{
        font-size: 14px;
        font-weight: 100;
    }
`;

export const HomeCardImgDiv=styled.div`
    display: flex;
    align-items: center;
    margin: 40px 0 0 0;
`;

export const HomeCardImgText2=styled.p`
    font-size: 14px;
    /* margin-left: 20px; */
    line-height: 20px;
    font-weight: bold;
    color: #29365D;
    span{
        font-weight: 100;
        color: grey;
    }
`;