import React, { useState, useEffect } from 'react';
import { HomeVideo } from '../../Home/style';
import HomeVideo1 from '../../../../assets/videos/news2.mp4'
import { baseLink } from '../../../../baseLink';
import axios from 'axios';

function MainPage() {

    const [state, setState] = useState([])

    const getData = () => {
        axios
            .get(baseLink + "video-news")
            .then((res) => {
                setState(res.data)
            })
            .catch((err) => {
                console.log(err);
            })
    };

    useEffect(() => {
        getData();
    }, [])

    return (
        <div style={{margin:"50px 0"}}>
            <video src={HomeVideo1} style={{ width: "100%" }} controls ></video>
            <div style={{ display: "flex", justifyContent: "space-evenly" }}>
                {state.map((value) => (
                    <div style={{ textAlign: "center", width: "24%" }}>
                        <HomeVideo controls src={baseLink + "files/" + value.videoLink}></HomeVideo>
                        <p>{value.title}</p>
                    </div>
                ))}
            </div>
        </div>
    )
}
export default MainPage;