import React, { useState, useEffect } from 'react';
import Slider from 'react-slick'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import axios from 'axios';
import { baseLink } from '../../../../baseLink';
import { Slider1Card, Slider1Card1 } from '../../Home/style';
import { HomeCardImgText2 } from '../../Home/HomeCardImg/style';

function AboutSlider(){

    const [state, setState] = useState([])

    const getData = () => {
        axios
            .get(baseLink + "carousel-news")
            .then((res) => {
                setState(res.data)
            })
            .catch((err) => {
                console.log(err);
            })
    };

    useEffect(() => {
        getData();
    }, [])

    var settings = {
        // dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 1000,
    };

    return(
        <div>
        <Slider1Card1>
          <Slider {...settings}>
              {state.map((value)=>(
                  <div>
                  <Slider1Card src={baseLink + "files/" + value.imageId} alt="#" />
                  <HomeCardImgText2 style={{ fontSize: "16px", width:"250px" }}>{value.title}<br /><span>{value.createdAt}</span></HomeCardImgText2>
                </div>
              ))}
          </Slider>
        </Slider1Card1>
      </div>
    )
}
export default AboutSlider;