import axios from 'axios';
import React, { useEffect } from 'react';
import { useState } from 'react';
import { baseLink } from '../../../../baseLink';
import HomeCardIcon from '../../Home/HomeCardIcon';
import AboutSlide from '../AboutSlid';
import AboutSlider from '../AboutSlider';
import MainPage from '../MainPage';
import MostPage from '../MostPage';
import { DFlex, MainCard, Naws, RightCards, WhatsNewWrapper } from './style';

function WhatsNew() {
    const [data, setData] = useState([])

    const [chosenCategory, setChosenCategory] = useState(0);

    const getData = () => {
        axios
            .get(baseLink + "whats-new")
            .then((res) => {
                setData(res.data);
            })
            .catch(
                (err) => {
                    console.log(err)
                }
            )
    };

    useEffect(() => {
        getData();
    }, [])

    return (
        <div>
            <div style={{display:"flex"}}>
                <WhatsNewWrapper>
                    <Naws>
                        {data?.map(({ id, category }, index) => (
                            <li onClick={() => setChosenCategory(index)} className={chosenCategory === index ? "active" : ""} key={id}>{category}</li>
                        ))}
                    </Naws>
                    <DFlex>
                        <MainCard>
                            <img src={baseLink + "files/" + data?.[chosenCategory]?.imageLink} alt="#" />
                            <p><b>{data?.[chosenCategory]?.title}</b></p>
                            <p><small>by {data?.[chosenCategory]?.author}-{data?.[chosenCategory]?.createdAt}</small></p>
                            <p>{data?.[chosenCategory]?.description}</p>
                        </MainCard>
                        <RightCards>
                            {data?.[chosenCategory]?.whatsNewList?.map(({ id, title, createdAt, imageLink }) => (
                                <DFlex>
                                    <img src={baseLink + "files/" + imageLink} alt="" />
                                    <div>
                                        <p><b>{data?.[chosenCategory]?.category}</b></p>
                                        <p>{title}</p>
                                        <p><small>{createdAt}</small></p>
                                    </div>
                                </DFlex>
                            ))}
                        </RightCards>
                    </DFlex>
                </WhatsNewWrapper>
                <HomeCardIcon />
            </div>
            <MostPage />
            <AboutSlide />
            <MainPage />
            <AboutSlider />
        </div>
    )
}
export default WhatsNew;