import styled from "styled-components";

export const AboutCard1=styled.div`
    padding: 50px 180px 0px 180px;
`;
export const AboutCardWrapper=styled.div`
    width: 220px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
`;

export const AboutCardLine=styled.div`
    width: 50px;
    height: 3px;
    background-color: #EB566C;
`;

export const AboutCardTitle=styled.p`
    font-size: 26px;
    font-weight: bold;
    color: #051460;
`;

export const AboutCardText=styled.p`
    width: 860px;
    color: grey;
    line-height: 33px;
    margin-bottom: 25px;
    font-size: 15px;
`;

