import styled from "styled-components";

export const AboutWrapper=styled.div`
    display: flex;
    align-items: center;
    padding: 50px 0;
`;

export const AboutCardImg=styled.div`
    width: 50%;
    background-color: #FF2143;
    padding: 20px 0 20px 0px 20px;
    img{
        height: auto;
        margin: 100px 140px 100px ;
    }
`;

export const AboutWrapper2=styled.div`
    display: flex;
    align-items: center;
    .line{
        width: 55px;
        height: 3px;
        margin-right: 12px;
        background-color: #FF2143;
    }
    p{
        font-size: 18px;
        color: #FF2143;
    }
`;

export const AboutWrapper1=styled.div`
    width: 50%;
    height: 776px;
    background-color: black;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 120px 80px;
`;

export const AboutTitle=styled.p`
    font-size: 50px;
    font-weight: 800;
    margin: 20px 0 45px;
`;

export const AboutText=styled.p`
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 20px;
`;

export const AboutText1=styled.p`
    line-height: 30px;
    margin-bottom: 50px;
`;

export const AboutBottom=styled.button`
    width: 210px;
    height: 55px;
    font-weight: bold;
    color: white;
    background-color: #FF2143;
    border: none;
    &:hover{
        background-color: white;
        color: black;
        transition-duration: .3s;
    }
`;


































