import styled from "styled-components";

export const FooterWrapper=styled.div`
    padding: 80px 0;
    background-color: #070B11;
    color: white;
    margin-top: 10px; 
`;

export const FooterPage=styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
`;

export const FooterPage1=styled.div`
    .footer-text{
        width: 300px;
        line-height: 25px;
        color: grey;
        padding: 27px 0;
        font-size: 15px;
    } 
`;

export const FooterPage2=styled.div`
    .footer-text1{
        font-size: 20px;
        font-weight: bold;
        padding-bottom: 40px;
    }
`;

export const FooterPage3=styled.div`
    display: flex;
    align-items: center;
    font-size: 15px;
    margin-bottom: 20px;
`;

export const FooterPageText=styled.p`
    width: 280px;
    line-height: 20px;
    padding: 0 20px;
    transition-duration: .5s;
    &:hover{
        color: red;
    }
    .footer-text3{
        font-size: 13px;
        color: grey;
        margin-top: 8px;
    }
`;