import React, { useEffect, useState } from 'react'
import axios from 'axios'

function Axios() {

    const [header, setHeader] = useState([])

    const [process, setProcess] = useState({
        loading: false,
        error: false,
        success: false,
    });

    const baseLink = "https://ibs-school.herokuapp.com/api/v1/"

    const getData = () => {
        setProcess({
            loading: true,
            error: false,
            success: false,
        })
        axios
            .get(baseLink + "header-news")
            .then((res) => {
                setProcess({
                    loading: false,
                    error: false,
                    success: true,
                })
                console.log(res.data);
                setHeader(res.data)
            })
            .catch(
                (err) => {
                    setProcess({
                        loading: false,
                        error: true,
                        success: false,
                    })
                    console.log(err);
                }
            );
    }

    useEffect(() => {
        getData()
    }, [])

    return (
        <div>
            {process.loading?<h1>Sabr...</h1>:""}
            {process.error?<h1>Error</h1>:""}
            {/* {header.length >0 ? header[0].category:""} */}
            {header?.[0]?.category}
            {header.map(({ id, category, title, createdAt, author, imageLink }) => (
                <div key={id}>
                    <p>{author}</p>
                    <p>{category}</p>
                    <p>{title}</p>
                    <p>{createdAt}</p>
                    <p>{id}</p>
                    <img src={`${baseLink}files/${imageLink}`} alt="#" />
                    <hr />
                </div>
            ))}
        </div>
    )
}

export default Axios
