import styled from "styled-components";

export const HomeCard1=styled.div`
    width: 160px;
    padding: 12px;
    display: flex;
    align-items: center;
    border: 1px solid lightgrey;
    background-color: white;
    border-radius: 5px;
    margin-bottom: 5px;
    p{
        width: 50px;
        font-size: 14px;
        font-weight: bold;
        margin-left: 15px;
        span{
            font-size: 12px;
            color: grey;
        }
    }
`;

export const HomeCardIcon1=styled.div`
    font-size: 30px;
    color: #3A5998;
`;


export const HomeCardIcons=styled.div`
    width: 360px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 35px 15px;
    background-color: white;
    border-radius: 10px;
    margin-bottom: 40px;
`;