import React from 'react';
import { HomePages1, HomePageText, HomePageText1, HomePageTitle } from './style';

function HomePages(props){
    return(
        <HomePages1>
            <img src={props.img} alt="#" />
            <div>
                <HomePageTitle>{props.text1}</HomePageTitle>
                <HomePageText>{props.text2}</HomePageText>
                <HomePageText1>{props.text3}</HomePageText1>
            </div>
        </HomePages1>
    )
}
export default HomePages;