import React from 'react'
import { Link } from 'react-router-dom'
import { NavbarIcons, NavbarUl, NavbarWrapper } from './style'

function Navbar() {
  return (
    <NavbarWrapper>
      <NavbarUl>
        <li>
          <Link to="/" className="nav-li">
            HOME
          </Link>
        </li>
        <li>
          <Link to="/about" className="nav-li">
            ABOUT
          </Link>
        </li>
        <li>
          <Link to="/category" className="nav-li">
            CATEGORY
          </Link>
        </li>
        <li>
          <Link to="/latest-news" className="nav-li">
            LATEST NEWS
          </Link>
        </li>
        <li>
          <Link to="/pages" className="nav-li">
            PAGES
          </Link>
        </li>
        <li>
          <Link to="/contact" className="nav-li">
            CONTACT
          </Link>
        </li>
      </NavbarUl>
      <NavbarIcons>
        <i class="fab fa-facebook-f"></i>
        <i class="fab fa-twitter"></i>
        <i class="fab fa-instagram"></i>
        <i class="fab fa-youtube"></i>
        <i class="fas fa-search"></i>
      </NavbarIcons>
    </NavbarWrapper>
  )
}
export default Navbar
