import React, { useState, useEffect } from 'react';
import { baseLink } from '../../../../baseLink';
import { HomeCard1, HomeCardIcon1, HomeCardIcons } from './style';
import axios from "axios"

function HomeCardIcon() {

    const [data, setData] = useState([])

    const getData = () => {
        axios
            .get(baseLink + "social-media")
            .then((res) => {
                setData(res.data);
            })
            .catch(
                (err) => {
                    console.log(err)
                }
            )
    };

    useEffect(() => {
        getData();
    }, [])


    return (
        <HomeCardIcons>
            {/* {data.map((value) => (
            <HomeCard1>
                <HomeCardIcon1>
                    {value.socialMediaName==="facebook"?<i class="fab fa-facebook-square"/>:
                    value.socialMediaName==="twitter"?<i class="fab fa-twitter-square"></i>:value.socialMediaName==="instagram"?<i class="fab fa-instagram-square"></i>:value.socialMediaName==="youtube"?<i class="fab fa-youtube-square"></i>:""}
                </HomeCardIcon1>
                <p>{value.numberOfFollowers} <span>Fans</span> </p>
            </HomeCard1>
            ))} */}
            <HomeCard1>
                <HomeCardIcon1>
                    <i class="fab fa-twitter-square"></i>
                </HomeCardIcon1>
                <p><span>Fans</span></p>
            </HomeCard1>
            <HomeCard1>
                <HomeCardIcon1>
                    <i class="fab fa-youtube-square"></i>
                </HomeCardIcon1>
                <p><span>Fans</span></p>
            </HomeCard1>
            <HomeCard1>
                <HomeCardIcon1>
                    <i class="fab fa-youtube-square"></i>
                </HomeCardIcon1>
                <p><span>Fans</span></p>
            </HomeCard1>
            <HomeCard1>
                <HomeCardIcon1>
                    <i class="fab fa-facebook-square" />
                </HomeCardIcon1>
                <p><span>Fans</span></p>
            </HomeCard1>
        </HomeCardIcons>

    )
}
export default HomeCardIcon;


