import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { baseLink } from '../../../../baseLink';
import Slider from 'react-slick'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { HomeBox3 } from '../../Home/style';
import { HomeCardImgText2 } from '../../Home/HomeCardImg/style';

function AboutSlide() {

    const [state, setstate] = useState([])

    const getData = () => {
        axios
            .get(baseLink + "trending-news")
            .then((res) => {
                setstate(res.data)
            })
            .catch((err) => {
                console.log(err);
            })
    };

    useEffect(() => {
       getData();
    }, [])

    var settings = {
        // dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 1000,
    };

    return (
        <HomeBox3>
            <p style={{ fontSize: "40px", fontWeight: "bold", color: "#051441", marginBottom: "25px" }}>Trending News</p>
            <Slider {...settings}>
                {state.map((value) => (
                    <div style={{display:"flex"}}>
                        <img src={baseLink + "files/" + value.imageId} alt="#" style={{ width: "330px" }} />
                        <HomeCardImgText2 style={{ fontSize: "16px", width:"250px" }}>{value.title}<br /><span>{value.createdAt}</span></HomeCardImgText2>
                    </div>
                ))}
            </Slider>
        </HomeBox3>
    )
}
export default AboutSlide;